import React, { useEffect, useState } from 'react';
import { LandingPage } from './LandingPage';

export default () => {

  const [[fullWidth, fullHeight], setDimension] = useState([window.innerWidth, window.innerHeight]);
  useEffect( _ => {
    const resizeEvent = _ => setDimension( _ => [window.innerWidth, window.innerHeight]);
    window.addEventListener("resize", resizeEvent);
    return _ => window.removeEventListener("resize", resizeEvent);
  }, [])

  return (
    <LandingPage width={fullWidth} height={fullHeight} isMobile={false} />
  );
}