const { useEffect } = require("react");

export const logTouch = ({touches, targetTouches, changedTouches}) => {
  const [touch] = touches;
  if (touch) {
    const {clientX, clientY} = touch;
    console.log(clientX, clientY)
  }
  console.log(targetTouches, changedTouches)
}

export const usePreventScreenDragging = _ => useEffect( _ => {
  document.addEventListener("touchmove", event => event.preventDefault(), {passive: false});
}, []);
